@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
.coin-container{display:flex;flex-direction:column;padding:20px 15px;width:100%}.wrapper{display:flex;flex-direction:column;width:100%;height:auto;padding:30px 0 20px;border-bottom:1px solid #FCF6BA}@media (min-width: 575px){.wrapper{flex-direction:row;justify-content:space-around;width:100%}}.wrapper .coin{display:flex;align-items:center;justify-content:space-evenly;margin-bottom:20px}@media (min-width: 575px){.wrapper .coin{justify-content:flex-start;flex-direction:column;width:30%}}.wrapper .coin__img{max-width:100px}@media (min-width: 575px){.wrapper .coin__img{margin:0 auto 20px}}.wrapper .coin-data{line-height:28px}@media (min-width: 575px){.wrapper .coin-data{width:50%;margin-left:30px}}.wrapper .coin-data__exchange{display:flex}.wrapper .coin-data__percent{width:100px;margin:0 10px;letter-spacing:1px}.red{display:flex;align-items:center;justify-content:center;color:#f00606}.red span{margin-left:10px;font-size:24px;font-weight:700;color:#f00606}.green{display:flex;color:#11d811}.green span{margin-left:10px;font-size:24px;font-weight:700;color:#11d811}

*{box-sizing:border-box;margin:0;padding:0;font-family:'Montserrat', sans-serif;color:#fff}body{background:#3c3b3f;background:linear-gradient(270deg, #3c3b3f 0%, #605c3c 100%)}.coin-app{display:flex;flex-direction:column;align-items:center;max-width:970px;width:100%;height:auto;margin:0 auto;padding:60px 10px 10px;color:#fff}.coin-app__search{display:flex;flex-direction:column;justify-content:center;align-content:center;max-width:400px;margin-bottom:40px}.coin-app__text{margin-bottom:32px;text-align:center}.coin-form{width:80%;margin:0 auto}.coin-input{width:100%;height:50px;padding-left:16px;border-radius:4px;border:none;background-image:linear-gradient(-225deg, #FCF6BA 0%, #AA771C 100%);color:#504e4e;font-weight:700}.coin-input::-webkit-input-placeholder{color:#504e4e;font-weight:700}.coin-input:-ms-input-placeholder{color:#504e4e;font-weight:700}.coin-input::placeholder{color:#504e4e;font-weight:700}

